/* 기본 스타일 초기화 */
* {
  margin: 0;
  padding: 0;
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-style: normal;
}

.our-web{
  min-height: 100vh;
  /* display: flex;
  flex-direction: column; */
}

.top-deco {
  width: 100%;
  height: 45px;
  background-color: #676767;
}

.title {
  width: 100%;
  height: 120px;
  align-content: center;
  text-align: center;
  font-size: 60px;
  font-weight: 600;
}

.RP {
  width: 100%;
  height: 150px;
  align-content: center;
  text-align: center;
}

.RP-top {
  font-size: 50px;
  font-weight: 600;
}

.RP-mid {
  font-size: 40px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.3);
}

.cate-button-sec {
  width: 1320px;
  height: 40px;
  margin: auto;
  display: flex;
  justify-content: space-between;
}

.cont-RP {
  width: 1320px;
  color: #d9d9d9;
  margin: auto;
}

.category-button {
  margin-top: 10px;
  display: flex;
  align-items: center;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  width: 110px;
  height: 30px;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.category-button:hover .cate-text {
  font-weight: 600;
}

.category-button:hover .hamburger-icon {
  background-color: #000000;
}

.category-button:hover .hamburger-icon span {
  background-color: #eaeaea;
}

.hamburger-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px;
  width: 30px;
  height: 30px;
}

.hamburger-icon span {
  display: block;
  width: 23px;
  height: 2px;
  background-color: #000000;
}

.cate-text {
  font-size: 20px;
  color: #000000;
  font-weight: 500;
}

.cate-menu {
  width: 1320px;
  height: 450px;
  /* background-color: aqua; */
}

.menu {
  display: flex;
  background-color: #FFFFFF;
  border: none;
  position: absolute;
  width: 1320px;
  height: 450px;
  box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15), 0px 1px 3px rgba(0, 0, 0, 0.3);
}

.m-left {
  flex: 0 0 165px;
  display: flex;
  flex-direction: column;
  border: none;
  /* background-color: antiquewhite; */
}

.l-btn-container {
  margin-top: 25px;
  margin-left: 10px;
}

.menu-item {
  padding-top: 6.5px;
  text-align: left;
  cursor: pointer;
  border: none;
  background: none;
  font-size: 12px;
  font-weight: 600s;
  width: 145px;
  height: 25px;
}

.menu-item.active {
  color: #e6c619;
  text-decoration-line: underline;
}

.m-right {
  flex: 0 0 1145px;
  padding: 25px 0 10px 0;
  margin-left: 10px;
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: flex-start;
  /* background-color: aqua; */
}

.vertical-line{
  position: absolute;
}

.v-line {
  position: absolute;
  left: -10px; /* 제목 왼쪽 10px */
  top: 0px;
  width: 0.5px;
  height: 400px; /* 원하는 높이로 설정 */
  border-left: 1px solid #eaeaea;
}

.m-sect {
  flex-direction: column;
  display: flex;
  align-items: center;
  /* background-color: darkgreen; */
}

.m-sect-title {
  font-weight: 600;
  text-align: left;
  align-items: center;
  /* background-color: chartreuse; */
  width: 145px;
  height: 25px;
  font-size: 12px;
  padding-top: 6.5px;
  position: relative; /* 제목만 상대적으로 조정 */
}

.m-sect-items {
  display: flex;
  /* 그룹의 항목들을 가로로 배치 */
  flex-direction: column;
  align-items: center;
  /* background-color: cornflowerblue; */
}

.m-sect-item {
  display: block;
  text-align: center;
  border: none;
  background: none;
  cursor: pointer;
  text-align: left;
  /* background-color: coral; */
  font-size: 10px;
  width: 145px;
  height: 20px;
  font-weight: 500;
  color: #949494;
}

/*검색창*/
.search{
  width: 240px;
  height: 40px;
  border: 1.6px solid #d9d9d9;
  border-radius: 20px;
  box-sizing: border-box;
  display: flex;
}

.inner-txt{
  box-sizing: border-box;
  border: 0;
  /* padding: 10.4px; */
  align-content: center;
  text-align: left;
  font-size: 16px;
  font-weight: 400;
  color: #b3b3b3;
  width: 183px;
  background: none;
  padding-left: 10.4px;
}

/*하단 footer 태크 안에 들어갈 것들*/
.Last{
  background-color: #949494;
  width: 100%;
  height: 470px;
  margin-top: 1440px;
}